import _ from 'lodash';
import helpers from './helpers/helpers.js';
import configService from './services/config.js';

import React from 'react';
import ReactDom from 'react-dom';
import { Router, match} from 'react-router';
import createBrowserHistory from 'history/lib/createBrowserHistory';
import useScroll from 'scroll-behavior/lib/useStandardScroll';
import queryString from 'query-string';
import { Provider } from 'react-redux';

import routes from './routes.js';
import storeService from './store/store.js';

var history = useScroll(createBrowserHistory)(),
    session = sessionService.getSession(),
    store,
    dispatch,
    lastStateLocation,
    unlisten;

configService.init(window.__config);
storeService.init(window.__state);
store = storeService.get();
dispatch = store.dispatch;

function route(location, callback) {

    match({routes, location}, (error, redirectLocation, renderProps) => {

        var query = queryString.parse(location.search),
            params = renderProps.params,
            component = renderProps.components[renderProps.components.length - 1],
            pageName = component.displayName.replace(/Controller/i, '').replace(/^Connect\(/i, '').replace(/\)/, '').toLowerCase(),
            initStore = (component && component.WrappedComponent && component.WrappedComponent.initStore)
                ? () => component.WrappedComponent.initStore(dispatch, params, query)
                : () => Promise.resolve(),
            state;

        Promise.resolve().then(
            () => {
                // grab current store state
                state = store.getState();

                callback(initStore);
            }

        ).catch((error) => console.error(error.stack));

    });
}

unlisten = history.listen(function(location) {

    lastStateLocation = store.getState().location;

    store.subscribe(function () {

        var state = store.getState(),
            stateLocation = state.location,
            newUrl;

        if (lastStateLocation.path !== stateLocation.path || queryString.stringify(lastStateLocation.query) !== queryString.stringify(stateLocation.query)) {
            newUrl = _.trim(`${stateLocation.path}?${queryString.stringify(stateLocation.query)}`, '?');
            history.pushState(null, newUrl);
            lastStateLocation = stateLocation;
        }

    });

    route(location, function (initStore) {
        unlisten();
        ReactDom.render(<Provider store={store}><Router history={history}>{routes}</Router></Provider>, document, (hello) => initStore());
    });

});

history.listenBefore(function (location, callback) {

    var query = queryString.parse(location.search),
        search = _.trim(location.search, '?'),
        stateLocation = store.getState().location,
        stateSearch = queryString.stringify(stateLocation.query);

    if ((stateLocation.path === location.pathname) && (stateSearch === search)) {
        callback();
    } else {

        route(location, (initStore) => {
            initStore();
            callback()
        });
    }

});

window.h = history;


